@use '../../style/mixins';
$min-item-size: 0.5rem;
$default-item-color: var(--chart-item-color, rgb(var(--contrast-1100), 0.8));

/**
* @prop --chart-background-color: Defines the background color of the chart. Defaults to `transparent` for _most_ chart types.
* @prop --chart-item-color: If no color is defined for chart items, this color will be use. Defaults to `rgb(var(--contrast-1100), 0.8)`.
* @prop --chart-item-divider-color: Defines the color that visually separates items in some charts, such as `stacked-bar` chart items. Defaults to `rgb(var(--color-white), 0.6)`.
* @prop --chart-axis-line-color: Defines color of the axis lines. Defaults to `--contrast-900`. Note that lines have opacity as well, and get opaque on hover.
* @prop --chart-item-border-radius: Defines the roundness of corners of items in a chart. Defaults to different values depending on the chart type. Does not have any effect on `pie` and `doughnut` types.
*/

:host(limel-chart) {
    --chart-axis-line-color: var(
        --limel-chart-axis-line-color,
        rgb(var(--contrast-900))
    );
    box-sizing: border-box;
    isolation: isolate;

    display: flex;
    flex-direction: column;
    gap: 0.5rem;
    width: 100%;
    height: 100%;
    min-width: 0;
    min-height: 0;
    padding: var(--limel-chart-padding);
}

table {
    // Since these are mainly "resets", no styles should be before them.
    all: unset;
    border-collapse: collapse;
    border-spacing: 0;
    empty-cells: show;

    position: relative;
    display: flex;
    width: 100%;
    height: 100%;
    min-width: 0;
    min-height: 0;

    colgroup,
    thead,
    tbody,
    tr,
    th,
    td {
        all: unset;
    }

    caption,
    colgroup,
    thead,
    tfoot,
    th,
    td {
        @include mixins.visually-hidden;
    }
}

*,
*:before,
*:after {
    box-sizing: border-box;
}

.chart {
    position: relative;
    flex-grow: 1;
    width: 100%;
    height: 100%;
    min-height: 0;
    min-width: 0;

    &:has(.item:hover),
    &:has(.item:focus-visible) {
        .item {
            opacity: 0.4;
        }
    }
}

.item {
    @include mixins.visualize-keyboard-focus;
    transition:
        background-color 0.2s ease,
        box-shadow 0.2s ease,
        filter 0.2s ease,
        opacity 0.4s ease;
    cursor: help;

    &:focus-visible,
    &:hover {
        opacity: 1 !important;
    }

    &[role='button'] {
        cursor: pointer;
    }
}

limel-spinner {
    margin: auto;
}

.legend {
    display: flex;
    align-self: center;
    max-width: 80%;
    font-size: small;

    background-color: rgb(var(--contrast-500));
    border-radius: 0.5rem;
    padding: 0.5rem;

    .legend-content {
        display: flex;
        width: 100%;
        overflow-x: auto;
        gap: 1rem;

        @include mixins.fade-out-overflowed-content-on-edges(horizontally);
    }

    //     // Change to column layout when chart has portrait orientation
    //     :host(.has-orientation-portrait) & {
    //         flex-direction: column;
    //         overflow-x: visible;
    //         overflow-y: auto;
    //         height: 100%;
    //         max-height: 200px; // Limit height for vertical scrolling

    //         // Remove horizontal fade and add vertical fade
    //         -webkit-mask-image: none;
    //         mask-image: none;
    //         padding-left: 0;
    //         padding-right: 0;

    //         @include mixins.fade-out-overflowed-content-on-edges(vertically);
    //     }
    // }

    // // Change to row layout when chart has portrait orientation
    // :host(.has-orientation-portrait) & {
    //     flex-direction: row;
    //     max-width: 100%;
    //     max-height: 250px; // Limit height in portrait mode
    //     align-self: stretch; // Take full width in portrait
    // }
}

.legend-item {
    display: flex;
    align-items: center;
    gap: 0.25rem;
    cursor: pointer;
    user-select: none;
    white-space: nowrap; // Prevent text wrapping
    flex-shrink: 0; // Prevent items from shrinking

    &--hidden {
        opacity: 0.5;
    }

    limel-badge {
        transition:
            opacity 0.2s,
            filter 0.2s;
    }

    &:hover limel-badge {
        filter: brightness(1.2);
    }
}

@mixin line(
    $direction: vertical,
    $color: rgb(var(--contrast-800), 0.4),
    $position: center
) {
    @if $direction == vertical {
        background: linear-gradient(to bottom, $color 0%, $color 100%)
            $position/1px
            100%
            no-repeat;
    } @else if $direction == horizontal {
        background: linear-gradient(to right, $color 0%, $color 100%)
            $position/100%
            1px
            no-repeat;
    }
}

